import strategy from './strategy.svg';
import content from './content.svg';
import data from './data.svg';
import research from './research.svg';

export default {
  icon: strategy,
  title: '策略',
  text: '透過無數次的線上溝通實驗經驗，設定策略解決溝通問題、達成目標！',
  content: [
    {
      icon: research,
      label: '溝通對象研究',
      text: '不夠瞭解潛在溝通對象嗎？透過各種線上、線下的觀察與訪談畫出輪廓。',
    },
    {
      icon: content,
      label: '資料處理',
      text: '不知道要如何使用資料？讓我們幫你以科學的方式和資料相處。',
    },
    {
      icon: data,
      label: '內容設計',
      text: '覺得手上的資訊不夠有趣？讓我們一起創造出客戶會喜歡的故事。',
    },
  ]
};



