import promotion from './promotion.svg';
import choice from './choice.svg';
import schedule from './schedule.svg';
import money from './money.svg';

export default {
  icon: promotion,
  title: '傳達',
  text: '掌握好的內容與資訊圖表後，讓我們來建議你如何使用、評估成效！',
  content: [
    {
      icon: money,
      label: '社群測試',
      text: '用最低的成本觀察不同溝通方式的表現，作為進階策略的依據。',
    },
    {
      icon: choice,
      label: '社群投放',
      text: '長期與優質的社群投放和精準分析團隊合作，最大化溝通效益。',
    },
    {
      icon: schedule,
      label: '檢討調整',
      text: '不斷動態檢討、調整投放策略，完美掌握群眾的反應。',
    },
  ]
};



