import React from 'react';

import Text from './Text';
import Box from './Box';
import Flex from './Flex';
import Border from './Border';
import BackgroundImage from './BackgroundImage';

const PageTitle = ({ children, sub, icon, top, ...props }) => (
  <Flex
    is={Border}
    borderBottom="1px solid"
    borderColor="lightBlue"
    justify="center"
    align="center"
    py="2em"
    mb="2em"
    {...props}
  >
    <Box position="relative">
      <Box
        mx="auto"
        position={['static', null, 'absolute']}
        w="6em"
        left={[null, null, '-1em']}
        transform={[null, null, 'translateX(-100%)']}
        top={top}
      >
        <BackgroundImage src={icon} />
      </Box>
      <Text.h1 f="2.5em" fontWeight="200" color="blue">{children}</Text.h1>
      <Text>{sub}</Text>
    </Box>
  </Flex>
);

export default PageTitle;



