import design from './design.svg';

import infographic from './infographic.svg';
import motion from './motion.svg';
import presentation from './presentation.svg';
import interaction from './interaction.svg';
import expo from './expo.svg';

export default {
  icon: design,
  title: '設計',
  text: '透過設計方法，把資訊視覺化，降低理解門檻並增加探索的樂趣！',
  content: [
    {
      icon: infographic,
      label: '資訊圖表',
      text: '一張圖就能把事情說清楚，溝通精準、散播容易。',
    },
    {
      icon: motion,
      label: '說明影片',
      text: '讓資訊動起來，90秒的影片就能說完一個充實精彩的故事。',
    },
    {
      icon: presentation,
      label: '商業簡報',
      text: '使你的簡報更有說服力，上台更有自信！',
    },
    {
      icon: interaction,
      label: '互動圖表',
      text: '讓使用者主動探索並自己篩選資訊，好的互動設計讓體驗加分。',
    },
    {
      icon: expo,
      label: '展場中的資訊圖表',
      text: '當資訊圖表走進立體空間，充實展覽內容與亮點。',
    },
  ]
};



