import React from 'react';

import PageTitle from '../../components/PageTitle';
import Container from '../../components/Container';
import Box from '../../components/Box';
import Text from '../../components/Text';
import Flex from '../../components/Flex';
import Border from '../../components/Border';
import BackgroundImage from '../../components/BackgroundImage';
import Link from '../../components/Link';
import DoubleLayerButton from '../../components/DoubleLayerButton';
import Header from '../../containers/Header';
import theme from '../../components/ThemeProvider/theme';

import data from './data';
import re from './re.svg';

const renderContent = ({ icon, label, text }, index) => (
  <Flex key={index} my="2em" px="2em" alignItems="center">
    <Box w={[1 / 4, null, 1 / 6]} mr="2em">
      <BackgroundImage src={icon} />
    </Box>
    <Box flex={1} align="left">
      <Text f="1.25em">{label}</Text>
      <Text>{text}</Text>
    </Box>
  </Flex>
);

const renderSection = ({ icon, title, text, content }, index) => (
  <Flex
    is={Border}
    borderBottom="2px solid"
    borderColor="lightGray"
    py="1em"
    flexDirection={['column', null, 'row']}
    align="center"
    key={index}
  >
    <Box align="center" w={[1, null, '40%']} pl="2em" pr={['2em', null, '4em']}>
      <Box px="25%" mb="2em">
        <BackgroundImage src={icon} />
      </Box>
      <Text f="1.5em" color="blue" my="1rem" fontWeight="bold">{title}</Text>
      <Text f="1.25em">{text}</Text>
    </Box>
    <Box w={[1, null, '60%']} mt={['2em', null, 0]}>
      {content.map(renderContent)}
    </Box>
  </Flex>
);

const About = (props) => {
  return (
    <div>
      <Header />
      <Container pt={theme.headerHeight} pb="2em" {...props}>
        <PageTitle
          icon={re}
          sub="從溝通策略、設計製作到推廣測試，我們都會在你身旁一起努力。"
          mb="1em"
        >
          我們的服務
        </PageTitle>
        {data.map(renderSection)}
        <Box py="2em" align="center">
          <Link to="/contact">
            <DoubleLayerButton px="1.5em" py="1em">合作洽詢</DoubleLayerButton>
          </Link>
        </Box>
      </Container>
    </div>
  );
};

export default About;



